@font-face {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/opensans.woff") format("woff"),url("../fonts/opensans.woff2") format("woff2");
    font-display: swap;
}

@font-face {
    font-family: Open Sans;
    font-style: normal;
    font-weight: 700;
    src: url("../fonts/opensansbold.woff2") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
    font-display: swap;
}

body {
    min-height: 100%;
    min-width: 320px;
    margin: 0;
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
}
