.social-media {
    width: 142px;
    box-sizing: border-box;
    padding-top: 25px;
    padding-bottom: 28px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.social-media__list {
    display: flex;
    justify-content: center;
    margin: 0;
    margin-left: 30px;
    margin-right: 30px;
    padding: 0;   
}

.social-media__item:not(:nth-child(3)) {
    margin-right: 28px;
}

